import React, { useEffect, useState } from 'react'

import firebase from '../../firebase';
import axios from 'axios'
import { useNavigate } from 'react-router-dom';

const UserJoin = () => {
    const [youName, setYouName] = useState("");
    const [youEmail, setYouEmail] = useState("");
    const [youPass, setYouPass] = useState("");
    const [youPassC, setYouPassC] = useState("");
    const [flag, setFlag] = useState(false);
    const [nameCheck, setNameCheck] = useState(false);
    const [nameInfo, setNameInfo] = useState("")
    const [authListenerEnabled, setAuthListenerEnabled] = useState(true);
    let navigate = useNavigate();

    useEffect(() => {
        let unsubscribe;
    
        if (authListenerEnabled) {
          unsubscribe = firebase.auth().onAuthStateChanged(user => {
            //    
          });
        }
    
        return () => {
          if (unsubscribe) {
            unsubscribe();
          }
        };
      }, [authListenerEnabled]);
    
      const JoinFunc = async (e) => {
        e.preventDefault();
    
        if (!(youName && youEmail && youPass && youPassC)) {
          return alert("    .");
        }
        if (youPass !== youPassC) {
          return alert("  .");
        }
    
        let createdUser = await firebase.auth().createUserWithEmailAndPassword(youEmail, youPass);
    
        await createdUser.user.updateProfile({
          displayName: youName,
        });
    
        setAuthListenerEnabled(false); //    
    
        let body = {
          email: createdUser.user.multiFactor.user.email,
          displayName: createdUser.user.multiFactor.user.displayName,
          uid: createdUser.user.multiFactor.user.uid,
          photoURL: "https://kr.object.ncloudstorage.com/react-blog1/post/websfonts.png",
        }
        console.log(body)
        await firebase.auth().signOut();
        
        axios.post("/api/user/join", body)
          .then((response) => {
            if (response.data.success) {
              alert(" .");
              navigate("/login");
              console.log(body)
            } else {
              alert(" .");
              console.log(body)
            }
          });
      };

    const NameCheckFunc = (e) => {
        e.preventDefault();
        if (!youName) {
            return alert(" !");
        }
        let body = {
            displayName: youName,
        }
        
        axios.post("/api/user/namecheck", body).then((response) => {
            if (response.data.success) {
                if (response.data.check) {
                    setNameCheck(true);
                    setNameInfo("  .");
                } else {
                    setNameInfo("   .");
                }
            }
        })
    }

    return (
        <div className='login__wrap'>
            <div className="login__header">
                <h3>Join</h3>
                <p> ?</p>
            </div>
            <form className='login__form'>
                <fieldset>
                    <legend className="blind"> </legend>
                    <div>
                        <label htmlFor="youName" className="required blind"></label>
                        <input
                            type="text"
                            id="youName"
                            name="youName"
                            placeholder=""
                            className="input__style"
                            autoComplete='off'
                            required
                            minLength={8}
                            value={youName}
                            onChange={(e) => setYouName(e.currentTarget.value)}
                        />
                    </div>

                    <div style={{ marginBottom: "10px" }}>
                        {nameInfo}
                        <button onClick={(e) => NameCheckFunc(e)}> </button>
                    </div>

                    <div>
                        <label htmlFor="youEmail" className="required blind"></label>
                        <input
                            type="email"
                            id="youEmail"
                            name="youEmail"
                            placeholder=""
                            className="input__style"
                            autoComplete='off'
                            required
                            minLength={8}
                            value={youEmail}
                            onChange={(e) => setYouEmail(e.currentTarget.value)}
                        />
                    </div>
                    <div>
                        <label htmlFor="youPass" className="required blind"></label>
                        <input
                            type="text"
                            id="youPass"
                            name="youPass"
                            placeholder=""
                            className="input__style"
                            autoComplete="off"
                            required
                            minLength={8}
                            value={youPass}
                            onChange={(e) => setYouPass(e.currentTarget.value)}
                        />
                    </div>
                    <div>
                        <label htmlFor="youPassC" className="required blind"> </label>
                        <input
                            type="text"
                            id="youPassC"
                            name="youPassC"
                            placeholder=" "
                            className="input__style"
                            autoComplete="off"
                            required
                            minLength={8}
                            value={youPassC}
                            onChange={(e) => setYouPassC(e.currentTarget.value)}
                        />
                    </div>
                    <button disabled={flag} type="submit" className="btn__style2 mt30" onClick={(e) => JoinFunc(e)}></button>
                </fieldset>
            </form>
        </div>
    )
}

export default UserJoin